import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue"
import uploadImages from "../../components/common/upload-images.vue"
import uniPopup from "../../components/uni-popup/uni-popup.vue"

let changelook = ['所有人可见', '仅自己可见'];

export default {
	components: {
		uniNavBar,
		uploadImages,
		uniPopup
	},
	data() {
		return {
			yinsi: '所有人可见',
			text: '',
			imglist: [],
			// 弹出公告参数
			showpopup: true,
			isget: false
		}
	},
	onBackPress () {
		// 如果用户有输入值猜才保存
		if (!this.text && this.imglist.length < 1) { return; }
		if (!this.isget) {
			this.baocun();
			return true;
		}
		
	},
	methods: {
		// 返回
		back () {
			uni.navigateBack({
				delta: 1
			});
		},
		// 发布
		sunbmit (){
			console.log("fabu");
		},
		// 下拉框(隐私)
		changelook () {
			uni.showActionSheet({
				itemList: changelook,
				success: (res) => {
						this.yinsi = changelook[res.tapIndex];
				}
			});
		},
		// 上传图片
		upload (arr) {
			this.imglist = arr;
			console.log(this.imglist);
		},
		// 隐藏弹出公告
		hidePopup () {
			this.showpopup = false;
		},
		// 保存为草稿
		baocun () {
			uni.showModal({
				content: '是否要保存为草稿？',
				canceltext: '不保存',
				sonfirmtext: '保存',
				success: (res) => {
					if (res.confirm) {
						console.log('保存');
					} else {
						console.log('不保存');
					}
					this.isget = true;
					uni.navigateBack({
						delta: 1
					})
				}
			});
		}
	}
}
